import axios from "axios";
import { axiosRequestFx } from ".";

axiosRequestFx.use(async ({ method, resource, data }) =>
  axios.request({
    method,
    url: resource,
    data,
    headers: {
      "Content-Type": "application/json",
      crossDomain: true,
      "Access-Control-Allow-Origin": "172.105.84.217",
      "Access-Control-Allow-Methods": "POST, GET, OPTIONS, PATCH, PUT",
    },
    withCredentials: true,
  })
);
