import { forward, sample } from "effector";
import { axiosRequestFx } from "../api";
import { OffersListGate } from "../app";
import {
  $offers,
  $selectedCategory,
  likeOfferFx,
  selectActivatedCategory,
  selectAllCategory,
  selectFavouriteCategory,
  selectFeaturedCategory,
  selectPopularCategory,
  selectPremiumCategory,
  fetchOffersFx,
  activateOfferFx,
  openOfferFx,
  like,
} from "./index";

const { REACT_APP_API_HOST } = process.env;

$selectedCategory
  .on(selectAllCategory, () => "all")
  .on(selectActivatedCategory, () => "activated")
  .on(selectFavouriteCategory, () => "favourite")
  .on(selectFeaturedCategory, () => "featured")
  .on(selectPopularCategory, () => "popular")
  .on(selectPremiumCategory, () => "premium");

// likes
sample({
  clock: like,
  source: $offers,
  fn: (offers, { id }) => {
    const state = offers.find((offer) => offer.id === id)?.favourite || false;
    return { id, favourite: !state };
  },
  target: likeOfferFx,
});

likeOfferFx.use(async ({ id, favourite }) => {
  await axiosRequestFx({
    method: "POST",
    resource: `${REACT_APP_API_HOST}/api/v1/bonus/favorite`,
    data: {
      id,
      favorite: favourite,
    },
  });
});

$offers
  .on(likeOfferFx.done, (offers, { params }) =>
    offers.map((offer) => (offer.id === params.id ? { ...offer, favourite: params.favourite } : offer))
  )
  .on(fetchOffersFx.doneData, (_, offers) => offers);

// fetch
forward({
  from: OffersListGate.open,
  to: fetchOffersFx,
});

fetchOffersFx.use(async () => {
  const response = await axiosRequestFx({
    method: "GET",
    resource: `${REACT_APP_API_HOST}/api/v1/bonus/offers`,
  });

  return response.data.data;
});

$offers
  .on(likeOfferFx, (offers, { id, favourite }) =>
    offers.map((offer) => (offer.id === id ? { ...offer, favourite } : offer))
  )
  .on(fetchOffersFx.doneData, (_, offers) => offers);

// activate
activateOfferFx.use(async ({ id }) => {
  const response = await axiosRequestFx({
    method: "GET",
    resource: `${REACT_APP_API_HOST}/api/v1/bonus/accept?id=${id}`,
  });

  return response.data.data;
});

forward({
  from: activateOfferFx.doneData,
  to: openOfferFx,
});

openOfferFx.use(async ({ url }) => {
  const tab = window.open(url, "_blank");
  tab?.focus();
});
