import {
  $isOfferPopupOpen,
  $tab,
  clickAboutTab,
  clickBonusTab,
  clickRulesTab,
  closeOfferPopup,
  openOfferPopup,
  $currentOfferId,
} from "./index";

$isOfferPopupOpen.on(openOfferPopup, () => true).on(closeOfferPopup, () => false);
$currentOfferId.on(openOfferPopup, (_, { offerId }) => offerId).reset(closeOfferPopup);

$tab
  .on(clickBonusTab, () => "bonus")
  .on(clickAboutTab, () => "about")
  .on(clickRulesTab, () => "rules")
  .reset(closeOfferPopup);
