import * as React from "react";
import styled from "styled-components/macro";

import { Dropdown } from "./Dropdown";
import { MenuPopup } from "./MenuPopup";

import logoSrc from "./img/logo.svg";
import logoMiniSrc from "./img/logo-mini.svg";
import bonusSrc from "./img/bonus.svg";
import hamburgerDefaultSrc from "./img/hamburger-default.svg";
import hamburgerTimesSrc from "./img/hamburger-times.svg";
import { useStore } from "effector-react";
import { $isMenuOpen, closeMenu, openMenu } from "../../store/topbar";
import { $offersCount } from "../../store/offers";

const Box = styled.div`
  height: 110px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: relative;

  @media (max-width: 480px) {
    height: 80px;
    padding: 0 15px;
  }
`;

const TopbarSide = styled.div`
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 0 -10px;
`;

const TopbarItem = styled.div`
  padding: 0 10px;
`;

const BonusCounter = styled.div`
  background-color: #f1f1f4;
  border-radius: 16px;
  display: flex;
  align-items: center;
  justify-content: space-between;

  & > span {
    font-family: "SFRounded", sans-serif;
    font-size: 24px;
    padding: 0 0 0 10px;
  }
`;

const Hamburger = styled.button`
  background-color: transparent;
  border: none;
  padding: 0;
  cursor: pointer;
`;

const DropdownBox = styled.div`
  position: absolute;
  right: 0;
  top: 95px;
  z-index: 100;

  @media (max-width: 480px) {
    display: none;
  }
`;

export const DefaultLogo = styled.img`
  display: block;

  @media (max-width: 480px) {
    display: none;
  }
`;

export const MiniLogo = styled.img`
  display: none;

  @media (max-width: 480px) {
    display: block;
  }
`;

export const Topbar: React.FC = () => {
  const isMenuOpen = useStore($isMenuOpen);
  const offersCount = useStore($offersCount);

  return (
    <>
      <Box key="box">
        <TopbarSide>
          <TopbarItem>
            <DefaultLogo src={logoSrc} alt="Inapp" />
            <MiniLogo src={logoMiniSrc} alt="Inapp" />
          </TopbarItem>
        </TopbarSide>

        <TopbarSide>
          <TopbarItem>
            <BonusCounter>
              <span>{offersCount}</span>
              <img src={bonusSrc} alt="б" />
            </BonusCounter>
          </TopbarItem>

          <TopbarItem>
            <Hamburger onClick={() => (isMenuOpen ? closeMenu() : openMenu())}>
              <img src={isMenuOpen ? hamburgerTimesSrc : hamburgerDefaultSrc} alt="меню" />
            </Hamburger>
          </TopbarItem>
        </TopbarSide>

        {isMenuOpen && (
          <DropdownBox>
            <Dropdown />
          </DropdownBox>
        )}
      </Box>

      <MenuPopup />
    </>
  );
};
