import { forward } from "effector";
import { axiosRequestFx } from "../api";
import { OffersListGate } from "../app";
import { $user, fetchUserFx } from ".";

const { REACT_APP_API_HOST } = process.env;

forward({
  from: OffersListGate.open,
  to: fetchUserFx,
});

fetchUserFx.use(async () => {
  return axiosRequestFx({
    method: "GET",
    resource: `${REACT_APP_API_HOST}/api/v1/get_user_data`,
  });
});

$user.on(fetchUserFx.doneData, (_, user) => user);
