import { combine, createEffect, createEvent, createStore } from "effector";
import {
  OfferCategory,
  Offer,
  ActivateOfferFxReqParams,
  ActivateOfferFxRes,
  LikeOfferFxReqParams,
  LikeOfferFxRes,
} from "./types";
// import { MOCK } from "./mock";

export const $offers = createStore<Offer[]>([]);
export const $offersCount = $offers.map((offers) => offers.length);

export const fetchOffersFx = createEffect<void, Offer[]>();

// categories
export const $selectedCategory = createStore<OfferCategory>("all");

export const selectAllCategory = createEvent();
export const selectActivatedCategory = createEvent();
export const selectFavouriteCategory = createEvent();
export const selectFeaturedCategory = createEvent();
export const selectPopularCategory = createEvent();
export const selectPremiumCategory = createEvent();

export const $isAllCategoryActive = $selectedCategory.map((category) => category === "all");
export const $isActivatedCategoryActive = $selectedCategory.map((category) => category === "activated");
export const $isFavouriteCategoryActive = $selectedCategory.map((category) => category === "favourite");
export const $isFeaturedCategoryActive = $selectedCategory.map((category) => category === "featured");
export const $isPopularCategoryActive = $selectedCategory.map((category) => category === "popular");
export const $isPremiumCategoryActive = $selectedCategory.map((category) => category === "premium");

// to render
export const $offersToRender = combine($offers, $selectedCategory, (offers, category) => {
  if (category === "activated") return offers.filter((offer) => offer.activated);
  if (category === "favourite") return offers.filter((offer) => Boolean(offer.favourite));
  if (category === "featured") return offers.filter((offer) => Boolean(offer.featured));
  if (category === "popular") return offers.filter((offer) => Boolean(offer.popular));
  if (category === "premium") return offers.filter((offer) => Boolean(offer.premium));

  // else or if all
  return offers;
});

// mark as favourite
export const like = createEvent<{ id: Offer["id"] }>();
export const likeOfferFx = createEffect<LikeOfferFxReqParams, LikeOfferFxRes>();

// activate
export const activateOfferFx = createEffect<ActivateOfferFxReqParams, ActivateOfferFxRes>();
export const openOfferFx = createEffect<ActivateOfferFxRes, void>();
