import * as React from "react";
import styled from "styled-components/macro";

import whiteArrowSrc from "./img/whiteArrow.svg";
import { GradientButtonProps } from "./types";

const Button = styled.button`
  width: 100%;
  padding: 15px 20px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: linear-gradient(109.61deg, #48c9ff 2.51%, #8461f7 45.91%, #ff8a71 91.16%);
  border: none;
  border-radius: 24px;
  cursor: pointer;

  @media (max-width: 480px) {
    font-size: 20px;
    border-radius: 16px;
  }

  & span {
    font-family: "SFRounded", sans-serif;
    font-size: 32px;
    color: #fff;

    @media (max-width: 480px) {
      font-size: 20px;
    }
  }

  & img {
    margin-left: 20px;

    @media (max-width: 480px) {
      width: 30px;
      height: 30px;
    }
  }
`;

const A = styled.a`
  width: 100%;
  padding: 15px 20px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: linear-gradient(109.61deg, #48c9ff 2.51%, #8461f7 45.91%, #ff8a71 91.16%);
  border: none;
  border-radius: 24px;
  cursor: pointer;
  text-decoration: none;

  & span {
    font-family: "SFRounded";
    font-size: 32px;
    color: #fff;
  }

  & img {
    padding-left: 20px;
  }
`;

export const GradientButton: React.FC<GradientButtonProps> = (props) => {
  const { text, onClick, href, tag, target } = props;

  if (tag === "a") {
    return (
      <A href={href} target={target}>
        <span>{text}</span>
        <img src={whiteArrowSrc} alt="" />
      </A>
    );
  }

  return (
    <Button type="button" onClick={onClick}>
      <span>{text}</span>
      <img src={whiteArrowSrc} alt="" />
    </Button>
  );
};
