import { createEvent, createStore } from "effector";

import { Tab } from "./types";
import { Offer } from "../offers/types";

// open-close
export const $isOfferPopupOpen = createStore(false);
export const $currentOffer = createStore<Offer | null | undefined>(null);
export const $currentOfferId = createStore<number>(0);

export const openOfferPopup = createEvent<{ offerId: number }>();
export const closeOfferPopup = createEvent();

// tabs
export const $tab = createStore<Tab>("bonus");

export const clickBonusTab = createEvent();
export const clickAboutTab = createEvent();
export const clickRulesTab = createEvent();

export const $isBonusTabActive = $tab.map((tab) => tab === "bonus");
export const $isAboutTabActive = $tab.map((tab) => tab === "about");
export const $isRulesTabActive = $tab.map((tab) => tab === "rules");
