import * as React from "react";
import styled from "styled-components";

import { OfferCategoryTabButton } from "../OfferCategoryTabButton";

import defaultSrc from "./img/default.png";
import activatedSrc from "./img/activated.png";
import favouriteSrc from "./img/favourite.png";
import featuredSrc from "./img/featured.png";
import popularSrc from "./img/popular.png";
import premiumSrc from "./img/premium.png";
import {
  $isActivatedCategoryActive,
  $isAllCategoryActive,
  $isFavouriteCategoryActive,
  $isFeaturedCategoryActive,
  $isPopularCategoryActive,
  $isPremiumCategoryActive,
  selectActivatedCategory,
  selectAllCategory,
  selectFavouriteCategory,
  selectFeaturedCategory,
  selectPopularCategory,
  selectPremiumCategory,
} from "../../store/offers";
import { useStore } from "effector-react";

const CategoriesBox = styled.div`
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
`;

const CategoryButtonBox = styled.div``;

export const OfferCategories: React.FC = () => {
  const isAllActive = useStore($isAllCategoryActive);
  const isActivatedActive = useStore($isActivatedCategoryActive);
  const isFavouriteActive = useStore($isFavouriteCategoryActive);
  const isFeaturedActive = useStore($isFeaturedCategoryActive);
  const isPopularActive = useStore($isPopularCategoryActive);
  const isPremiumActive = useStore($isPremiumCategoryActive);

  return (
    <CategoriesBox>
      <CategoryButtonBox>
        <OfferCategoryTabButton
          pic={defaultSrc}
          label="Все бонусы"
          active={isAllActive}
          onClick={() => selectAllCategory()}
        />
      </CategoryButtonBox>
      <CategoryButtonBox>
        <OfferCategoryTabButton
          pic={activatedSrc}
          label="Активированные"
          active={isActivatedActive}
          onClick={() => selectActivatedCategory()}
        />
      </CategoryButtonBox>
      <CategoryButtonBox>
        <OfferCategoryTabButton
          pic={favouriteSrc}
          label="Избранное"
          active={isFavouriteActive}
          onClick={() => selectFavouriteCategory()}
        />
      </CategoryButtonBox>
      <CategoryButtonBox>
        <OfferCategoryTabButton
          pic={featuredSrc}
          label="Рекомендуем"
          active={isFeaturedActive}
          onClick={() => selectFeaturedCategory()}
        />
      </CategoryButtonBox>
      <CategoryButtonBox>
        <OfferCategoryTabButton
          pic={popularSrc}
          label="Популярное"
          active={isPopularActive}
          onClick={() => selectPopularCategory()}
        />
      </CategoryButtonBox>
      <CategoryButtonBox>
        <OfferCategoryTabButton
          pic={premiumSrc}
          label="Премиум"
          active={isPremiumActive}
          onClick={() => selectPremiumCategory()}
        />
      </CategoryButtonBox>
    </CategoriesBox>
  );
};
